.nippleTop {
  border-radius: 100%;
  background: rgb(255, 181, 181);
  width: 60px;
  height: 60px;
  transition: background 500ms ease-in-out;
}

.nipple {
  background: #ffbfbf;
  transition: background 500ms ease-in-out;
}
.container[fast="true"],
.container[stretched="true"] {
  .nipple {
    background: rgb(255, 161, 172);
  }
  .nippleTop {
    background: rgb(245, 149, 160);
  }
}

.nippleBase {
  border-bottom: 6px solid rgba(0, 0, 0, 0.01);
}

.backNipple {
  border-bottom: 11px solid rgba(0, 0, 0, 0.02);
}
